<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
    <div id="searchBox">
        <div id="selectBox"> <!--   -->
            <span>Name : </span><input v-model="selectedName" @change="onChangeForName($event)" placeholder="Name" class="searchOpt1"> <!-- -->
            <span>Nickname : </span><input v-model="selectedNickname" @change="onChangeForNickname($event)" placeholder="Nickname" class="searchOpt1"> <!-- -->
            <span>Country : </span>
            <select v-model="selectedCountry" class="searchOpt2" @change="onChangeForCountry($event)"> <!-- -->
                <option selected>----</option>
                <option v-for="country in countries" :key ="country.name" v-bind:value="country.name" selected>{{ country.name }}</option>
            </select>
            <span>University : </span>
            <select v-model="selectedUniversity" class="searchOpt2" @change="onChangeForUniversity($event)"> <!-- -->
                <option selected>----</option>
                <option v-for="university in universities" :key ="university.name" v-bind:value="university.name" selected>{{ university.name }}</option>
            </select>
            <br>
            <span>Major (with UniversityName & UniversityCampusName & major) : </span>
            <select v-model="selectedUniversity" class="searchOpt2"> <!-- -->
                <option selected>----</option>
                <option v-for="university in universities" :key ="university.name" v-bind:value="university.name" selected>{{ university.name }}</option>
            </select>
            <select v-model="resultUniindex" class="searchOpt2" @change="UItoM($event)"> <!-- campus+ -->
                <option selected>----</option>
                <option v-for="uindex in uniindex" :key ="uindex.nameindex" v-bind:value="uindex.nameindex" selected>{{uindex.nameindex}}</option>
            </select>
            <select v-model="selectedMajor" class="searchOpt2" @change="onChangeForMajor($event)"> <!--, campus    -->
                <option selected>----</option>
                <option v-for="major in majors" :key ="major.major" v-bind:value="major.major" selected>{{ major.major }}</option>
            </select>
        </div>
        <br>
        <div id="searchedResult"> <!--   -->
            <table width="100%">
                <th>Name</th>
                <th>Nickname</th>
                <th>Country</th>
                <th>University</th>
                <th>Major</th>
                <th>Email</th>
                <tr class ="linedBoard" v-for ="searchedPerson in searchedPeople" :key ="searchedPerson.title">
                    <td align="center" style ="color : #566270;">{{ searchedPerson.name }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.nickname }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.country }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.universityName }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.major }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.email}}</td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        data () {
            return{
                people: [], //  
                countries : [{}], //  
                majors : [{}],
                universities : [{}],
                selectedName : '',
                selectedNickname: '',
                selectedCountry: '', //   
                selectedMajor: '',
                selectedUniversity: '',
                searchedPeople: [{}], //   .
                resultUniindex : '', //     
                uniindex:[{}], //  

            }
        },
        created: function(){
            //   
            axios.get('/api/universityList/country')
                .then((response)=>{
                    this.countries = response.data;
                });
            //   
            axios.get('/api/universityList/name')
                .then((response)=>{
                    this.universities = response.data;
                });
            //    major   .\

            //  posting();
            axios.post('/api/users/postByWhole', {
                    name: this.selectedName,
                    nickname: this.selectedNickname,
                    country: this.selectedCountry,
                    university: this.selectedUniversity,
                    majorUniversityIndex: this.resultUniindex,
                    major: this.selectedMajor,
                }
            ).then(response=>{
                this.searchedPeople = response.data;
            })

        },
        methods:{
            //university   campus  
            //campus   major 
            UItoM(event){
                var UNAME = this.selectedUniversity;
                var UNAMEINDEX = event.target.value;
                axios.get('/api/universityList/major/'+UNAME+'/'+UNAMEINDEX)
                    .then((response)=>{
                        this.majors = response.data;
                        this.resultUniindex = event.target.value;
                    })
            },


            //  //    ..
            onChangeForName(event){
                this.searchedPeople = [{}];
                this.selectedName = event.target.value;
                var nickname = this.selectedNickname;
                var country = this.selectedCountry;
                var university = this.selectedUniversity;
                var majorUniversityIndex = this.resultUniindex;
                var major = this.selectedMajor;
                    //  posting();
                    axios.post('/api/users/postByWhole', {
                            name: event.target.value,
                            nickname: nickname,
                            country: country,
                            university: university,
                            majorUniversityIndex: majorUniversityIndex,
                            major: major,
                        }
                    ).then(response=>{
                        this.searchedPeople = response.data;
                    })

            },
            onChangeForNickname(event){
                this.searchedPeople = [{}];
                this.selectedNickname= event.target.value;
                var name = this.selectedName;
                var country = this.selectedCountry;
                var university = this.selectedUniversity;
                var majorUniversityIndex = this.resultUniindex;
                var major = this.selectedMajor;

                    //  posting();
                    axios.post('/api/users/postByWhole', {
                            name: name,
                            nickname: event.target.value,
                            country: country,
                            university: university,
                            majorUniversityIndex: majorUniversityIndex,
                            major: major,
                        }
                    ).then(response=>{
                        this.searchedPeople = response.data;
                    })

            },
            onChangeForCountry(event){
                this.searchedPeople = [{}];
                this.selectedCountry = event.target.value;
                var name  = this.selectedName;
                var nickname = this.selectedNickname;
                var country = event.target.value;
                var university = this.selectedUniversity;
                var majorUniversityIndex = this.resultUniindex;
                var major = this.selectedMajor;

                    //  posting();
                    axios.post('/api/users/postByWhole', {
                            name: name,
                            nickname: nickname,
                            country: country,
                            university: university,
                            majorUniversityIndex: majorUniversityIndex,
                            major: major,
                        }
                    ).then(response=>{
                        this.searchedPeople = response.data;
                    })

            },
            onChangeForUniversity(event){
                this.searchedPeople = [{}];
                var UNAME = event.target.value;
                axios.get('/api/universityList/nameindex/'+UNAME)
                    .then((response)=>{
                        this.uniindex = response.data;
                        this.selectedUniversity = event.target.value;
                    });
                var name  = this.selectedName;
                var nickname = this.selectedNickname;
                var country = this.selectedCountry;
                var university = event.target.value;
                var majorUniversityIndex = this.resultUniindex;
                var major = this.selectedMajor;

                    //  posting();
                    axios.post('/api/users/postByWhole', {
                            name: name,
                            nickname: nickname,
                            country: country,
                            university: university,
                            majorUniversityIndex: majorUniversityIndex,
                            major: major,
                        }
                    ).then(response=>{
                        this.searchedPeople = response.data;
                    })

            },
            onChangeForMajor(event){
                this.searchedPeople = [{}];
                this.selectedMajor= event.target.value;
                var name  = this.selectedName;
                var nickname = this.selectedNickname;
                var country = this.selectedCountry;
                var university = this.selectedUniversity;
                var majorUniversityIndex = this.resultUniindex;
                var major = event.target.value;


                    //  posting();
                    axios.post('/api/users/postByWhole', {
                            name: name,
                            nickname: nickname,
                            country: country,
                            university: university,
                            majorUniversityIndex: majorUniversityIndex,
                            major: major,
                        }
                    ).then(response=>{
                        this.searchedPeople = response.data;
                    })

            }





        }

        //       .
    }
</script>

<style>
    #searchBox{
        border:3px solid #ffc952;
        height: auto;
        width: auto;
        margin-left:100px;
        margin-right:100px;
        align-content: center;
        text-align: center;
        margin-top:30px;
    }
    #selectBox{ /*   . 1*/
        width: auto;
        text-align: center;
        margin: auto;
        padding: 10px;
        border: 0.5px solid;
        border-color: #dddfe6;
        background: #dddfe6;
    }
    .searchOpt1{ /*  , .    . 2*/
        width: 100px;
        height: 24px;
        margin: 10px;
    }
    .searchOpt2{ /*  , , . 3*/
        width: 100px;
        height: 30px;
        margin: 10px;
    }
    #searchedResult{ /*   . 1*/
        text-align: center;
        width: auto;
        /*margin-left: 250px;*/
        margin: auto;
        border: 1px solid;
        border-color: #dddfe6;
    }
</style>